div.chickenTopBackground {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
  height: 74vh;
  min-height: 360px;
  z-index: 3;
  background-image: url(../images/background-top-chicken.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  margin-bottom: 120%;
}

div.chicken {
  text-align: center;
  position: absolute;
  top: 10%;
  width: 100%;
}
div.chicken img {
  width: 95%;
}

p.chickenDescription {
  position: absolute;
  top: 100%;
  color: #e66721;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  padding: 0px 40px 15px;
  margin-top: 50px;
}

.chickenPizzaAnimation {
  margin: 0 auto;
  background-position-y: 0%;
  background-size: 99.9%;
  background-repeat: no-repeat;
  margin-left: auto;
}

@media (min-width: 320px) {
  div.chickenTopBackground {
    margin-bottom: 90%;
  }
}

@media (min-width: 360px) {
  p.chickenDescription {
    padding: 15px 40px;
  }
}

@media (min-width: 500px) {
  p.chickenDescription {
    font-size: 20px;
    margin-right: 5%;
    margin-left: 5%;
    margin-top: 80px;
  }
}
@media (min-width: 550px) {
  p.chickenDescription {
    font-size: 26px;
  }
}

@media (min-width: 760px) {
  p.chickenDescription {
    font-size: 30px;
  }
}

@media (min-width: 1024px) {
  .chickenPizzaAnimation {
    margin: 0;
    height: 100%;
    background-position-y: 0%;
    background-size: 100%;
    background-repeat: no-repeat;
    margin-left: auto;
  }

  div.chickenTopBackground {
    width: 100%;
    height: 58vw;
    max-height: 1110px;
    background-image: url(../images/skos-desktop.png);
    background-repeat: no-repeat;
    background-size: contain;
    margin-bottom: 0;
  }

  div.chicken {
    text-align: left;
    top: 125px;
    margin-left: 8%;
  }
  div.chicken img {
    width: 35%;
  }
  p.chickenDescription {
    color: #fffad4;
    top: 19%;
    left: 0;
    font-size: 16px;
    width: 38%;
  }

  div.ingredients_desktop img.desktop_chickenIngredients {
    width: 25%;
    bottom: 10%;
    left: 14%;
  }
}
@media (min-width: 1200px) {
  p.chickenDescription {
    font-size: 20px;
  }
}
@media (min-width: 1365px) {
  p.chickenDescription {
    font-size: 22px;
  }
}
@media (min-width: 1439px) {
  p.chickenDescription {
    font-size: 24px;
  }
}
@media (min-width: 1599px) {
  p.chickenDescription {
    font-size: 25px;
  }
}
@media (min-width: 1920px) {
  p.chickenDescription {
    font-size: 32px;
  }
}
