div.chickenInformationsBackground {
  position: relative;
  padding: 10px 0;
  width: 100%;
  min-height: auto;
  background-position: inherit;
  background-size: auto;
  background-repeat: no-repeat;
  background-image: url(./images/skos3.png);
}

img.chickenArrow {
  width: 3%;
  margin: 20px auto;
  display: block;
  transform: rotate(180deg);
  content: url("./images/arrow-mobile.png");
}

.chickenNutritionalValues {
  display: flex;
  flex-direction: column;
  font-size: 14px;
  color: #fffad4;
  justify-content: space-around;
}

p.chickenTitle {
  font-size: 20px;
  margin-bottom: 5px;
  color: #fffad4;
}

div.chickenLineInformations {
  width: 180px;
  height: 5px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(./images/line.png);
  margin-bottom: 15px;
}
div.chickenLineAlergens {
  width: 80px;
  height: 5px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(./images/line.png);
}

div.chickenNutritionalValue {
  display: flex;
}

div.chickenNutritionalValue p {
  flex-basis: 20%;
  margin: 0;
}

.chickenAlergensDescription {
  color: #fffad4;
  display: none;
  transition: 0.3s;
}

@media (min-width: 768px) {
  div.chickenLineInformations {
    width: 310px;
  }
  div.chickenLineAlergens {
    width: 140px;
  }
  p.chickenTitle {
    font-size: 35px;
  }
  div.chickenNutritionalValue,
  .chickenAlergensDescription {
    font-size: 18px;
  }
}

@media (min-width: 1024px) {
  img.chickenArrow {
    margin: 20px auto;
    content: url("./images/arrow.png");
  }
  div.chickenInformationsBackground {
    display: block;
    height: 0;
    background-image: none;
  }

  p.chickenTitle {
    font-size: 23px;
    color: #e66721;
    font-weight: 700;
  }
  div.chickenLineInformations {
    width: 240px;
    height: 12px;
    background-image: url(./images/lineChicken.png);
    background-size: contain;
    background-position: center;
  }

  div.chickenLineAlergens {
    width: 120px;
    height: 11px;
    background-image: url(./images/lineChicken.png);
    background-position: center;
  }
  .chickenNutritionalValues {
    color: #e66721;
    transition: 0.1s;
  }

  div.chickenNutritionalValue,
  .chickenAlergensDescription {
    font-size: 12px;
  }

  .chickenAlergensDescription {
    color: #e66721;
    display: block;
  }
}
