.chickenInstructionTitle {
  color: #e66721;
  font-size: 20px;
  margin-bottom: 5px;
}

div.chickenLine {
  text-align: center;
  background-image: url(./images/line.png);
  width: 60%;
  height: 12px;
  background-repeat: no-repeat;
  background-size: contain;
}
div.chickenSteps div.step1,
div.chickenSteps div.step2,
div.chickenSteps div.step3 {
  display: flex;
  justify-content: flex-start;
  color: #e66721;
}

@media (min-width: 600px) {
  .chickenInstructionTitle {
    font-size: 35px;
  }
  div.chickenLine {
    height: 20px;
  }
}
